import { submit } from 'api/api'
import { mapActions, mapGetters } from 'vuex'
import md5 from 'md5'
export default {
  data() {
    return {
      phone: '',
      psd: ''
    }
  },
  created() {
    this.$watch('phone', (newPhone) => {
      this.phone = newPhone
    })
    this.$watch('psd', (newPsd) => {
      this.psd = newPsd
    })
  },
  methods: {
    cancel() {
      this.$router.back()
    },
    submit() {
      // 密码进行md5加密操作
      let pwd = md5(this.psd)
      let mobile = this.phone
      let is_mobile = 1
      let encrypt = 1
      submit(mobile,pwd,is_mobile,encrypt).then((res)=>{
        if(res.status === 1) {
          // 登录成功之后这里进行vuex状态管理操作
          let userAccount = res.msg
          this.saveUserAccount(userAccount)
          this.$router.back()
        }
      })
    },
    register() {
      this.$router.push({
        path: 'register'
      })
    },
    ...mapActions([
      'saveUserAccount'
    ])
  }
}



